/** Category Meta **/
.entry_cats {
  margin-bottom: 10px;
}

.entry_cats ul.post-categories {
  list-style: none;
  margin: 0px;
}

.entry_cats ul.post-categories li {
  display: inline-block;
  margin-right: 5px;
  margin-bottom: 5px;
}

.entry_cats ul.post-categories li a {
  display: inline-block;
  color: #fff;
  font-size: 13px;
  background-color: #FF3D00;
  padding: 4px 10px;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  line-height: 1;
}

.entry_cats ul.post-categories li a:hover {
  color: #fff;
  background-color: #010101;
}

/** Author, Date, and Comments Meta **/
.cm-post-meta {
  margin-top: 5px;
}

ul.post_meta {
  list-style: none;
  margin: 0px;
}

ul.post_meta li {
  display: inline-block;
  font-size: 14px;
  margin-right: 15px;
  margin-bottom: 0px;
}

ul.post_meta li a {
  color: #666666;
  font-size: 14px;
  -webkit-transition: all 0.7s ease;
  -moz-transition: all 0.7s ease;
  -ms-transition: all 0.7s ease;
  -o-transition: all 0.7s ease;
  transition: all 0.7s ease;
}

ul.post_meta li a {
  display: inline-block;
}

ul.post_meta li a:hover {
  color: #FF3D00;
  -webkit-transition: all 0.7s ease;
  -moz-transition: all 0.7s ease;
  -ms-transition: all 0.7s ease;
  -o-transition: all 0.7s ease;
  transition: all 0.7s ease;
}

ul.post_meta li a::before {
  font-family: "feather";
  font-size: 14px;
  margin-right: 5px;
}

ul.post_meta li.post_author a::before {
  content: "\e8e9";
}

ul.post_meta li.posted_date a::before {
  content: "\e83a";
}

ul.post_meta li.comments a::before {
  content: "\e891";
}

ul.post_meta li.entry_cats {
  color: #666666;
}

ul.post_meta li.entry_cats::before {
  font-family: "feather";
  font-size: 14px;
  margin-right: 5px;
  content: "\e90d";
}

/** Tag Meta **/
.post_tags a {
  display: inline-block;
  font-size: 14px;
  background-color: #FF3D00;
  padding: 4px 15px;
  margin-right: 10px;
  margin-bottom: 10px;
  color: #fff;
  text-transform: capitalize;
}

.post_tags a::before {
  content: "\e8d2";
  font-family: "feather";
  margin-right: 5px;
}

.post_tags a:hover {
  background-color: #010101;
  color: #fff;
}

.post-display-grid {
  margin-bottom: 20px;
}

.post-display-grid .card_content {
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.02);
  border: 1px solid rgba(0, 0, 0, 0.03);
}

.post-display-grid .post-format span {
  background-color: #010101;
  border-color: #010101;
  color: #fff;
  text-align: center;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  padding: 0px;
}

.post-display-grid .card_content .entry_cats {
  margin-bottom: 10px;
}

.post-display-grid .card_content .post_title h2 {
  font-size: 20px;
}

.post-display-grid .card_content .post_meta {
  margin-top: 10px;
}

