}

.col {
  float: left;
  width: 50%;
  padding-left: 15px;
  padding-right: 15px;
}

.mask {
  position: absolute;
  z-index: 9;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.7s ease;
  -moz-transition: all 0.7s ease;
